<template>
    <div id="hello">
        <loading v-show="(allArticles.length == 0)"></loading>
        <model-two :allArticles="allArticles"></model-two>
        <infinite-loading @infinite="infiniteHandler" ref="infiniteLoading">
            <span slot="no-results">没有更多了,下滑刷新↓</span>
            <span slot="no-more">没有更多了,下滑刷新↓</span>
        </infinite-loading>
    </div>
</template>

<script>
    import {ArticleData} from '@/ArticleData/ArticleData';
    import ModelTwo from '@/components/first/model/ModelTwo';
    import InfiniteLoading from 'vue-infinite-loading';
    import Loading from '@/components/first/loading';
    export default {
        name: 'All',
        components: {
            ModelTwo,
            InfiniteLoading,
            Loading,
        },
        data() {
            return {
                allArticles: [],
                page: 1,
            }
        },
        mounted() {
            ArticleData.getData({limit:20,tab:'weex'}).then((res) => {
                this.allArticles = res.data.data;
            })
        },
        methods: {
            infiniteHandler($state){
                ArticleData.getData({limit: 10,tab: 'weex',page: ++this.page}).then((res)=>{
                    if(res.data.data.length != 0){
                        this.allArticles = this.allArticles.concat(res.data.data);
                        $state.loaded();
                    }else if(res.data.data.length == 0) {
                        $state.complete();
                    }
                })
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>



