<template>
    <div id="hello">
        <loading v-show="allArticles.length == 0 && searchInfo.length != 0"></loading>
        <model-one :allArticles="allArticles"></model-one>
        <infinite-loading @infinite="infiniteHandler">
            <span slot="no-results">没有更多了,下滑刷新↓</span>
            <span slot="no-more">没有更多了,下滑刷新↓</span>
        </infinite-loading>
    </div>
</template>

<script>
    import {ArticleData} from '@/ArticleData/ArticleData';
    import ModelOne from '@/components/first/model/ModelOne';
    import InfiniteLoading from 'vue-infinite-loading';
    import Loading from '@/components/first/loading';
    export default {
        name: 'All',
        components: {
            Loading,
            ModelOne,
            InfiniteLoading,
        },
        props: ['searchInfo'],
        data() {
            return {
                allArticles: [],
                page: 1,
            }
        },
        watch: {
            searchInfo: function(n){
                if(!n) {
                    this.$emit('hideMe');
                    return;
                }else {
                    this.$emit('showMe');
                }
                var path = this.$route.path.split('/')[this.$route.path.split('/').length - 1];
                path = path == 'index' ? '' : path;
                ArticleData.getData({limit:100,tab:path}).then((res) => {
                    var reg = new RegExp(n,'i','g');
                    var results = res.data.data;
                    var filters = [];
                    for(var index = 0;index < results.length;index ++){
                        if(reg.test(results[index].title)){
                            filters.push(results[index]);
                        }
                    }
                    this.allArticles = filters;
                })
            },
        },
        methods: {
            infiniteHandler($state) {
                ArticleData.getData({limit:10,page:++this.page}).then((res)=>{
                    if(res.data.data.length != 0){
                        this.allArticles = this.allArticles.concat(res.data.data);
                        $state.loaded();
                    }else if(res.data.data.length == 0){
                        $state.complete();
                    }
                })
            },
        },
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">


</style>



