import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import First from '@/components/First'
import Login from '@/components/Login'
import Reg from '@/components/Reg'
import About from '@/components/About'
import All from '@/components/first/All'
import Jinghua from '@/components/first/Jinghua'
import Weex from '@/components/first/Weex'
import Share from '@/components/first/Share'
import Ask from '@/components/first/Ask'
import Job from '@/components/first/Job'
import Article from '@/components/article/Article'

Vue.use(Router)

export default new Router({

	routes: [
		{
			path: '/index',
			name: 'First',
			component: First,
			children: [
				{
					path: '/',
					component: All,
				},
				{
					path: '/detail',
					component: Article,
				},
				{
					path: 'good',
					component: Jinghua,
				},
				{
					path: 'weex',
					component: Weex,
				},
				{
					path: 'share',
					component: Share,
				},
				{
					path: 'ask',
					component: Ask,
				},
				{
					path: 'job',
					component: Job,
				},
			],
		},
		{
			path: '/',
			redirect: '/index',
		},
		{
			path: '/login',
			name: 'Login',
			component: Login,
		},
		{
			path: '/Reg',
			name: 'Reg',
			component: Reg,
		},
		{
			path: '/About',
			name: 'About',
			component: About,
		}
	]
})



