<template>
	<div id="app">
		<router-view/>
		<div class="wrapper">
			<div class="marginB"></div>
			<ul class="footer-nav">
				<li :class="pth=='index'?'changeBG':''">
					<router-link to="/">首页</router-link>
				</li>
				<li :class="pth=='login'?'changeBG':''">
					<router-link to="/login">登录</router-link>
				</li>
				<li :class="pth=='reg'?'changeBG':''">
					<router-link to="/reg">注册</router-link>
				</li>
				<li :class="pth=='about'?'changeBG':''">
					<router-link to="/about">关于</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import Router from 'vue-router'
export default {
	name: "App",
	computed : {
		pth : function(){
			return this.$route.path.split('/')[1];
		}
	}
};
</script>

<style scoped lang="scss">
@import './assets/scss/base.scss';
h1,
h2 {
  font-weight: normal;
}
.wrapper{
	margin: 0 auto;
	width: 100%;
	max-width: $maxWidth;
	.marginB{
		margin-top: rem(118px);
	}
}
.footer-nav{
	margin: 0 auto;
    width: 100%;
    max-width: $maxWidth;
    position: fixed;
    bottom: 0;
	z-index: 99;
    list-style: none;
    display: flex;
    background-color: #19612f;
    li{
        width: 25%;
        text-align: center;
        a{
            display: block;
            line-height: rem(100px);
			font-size: rem(34px);
			font-weight: bold;
            color: #fff;
            text-decoration: none;
            border-right: 1px solid #ccc;
        }
        &:last-child a{
			border-right: none;
		}
    }
	.changeBG{
		background-color: #ddd;
		a{color: #19612f;}
	}
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0 auto;
}
</style>



