<template>
    <div class="container">
        <h2 style="font-weight: bold;padding: 1em .3em 0;;">{{article.title}}</h2>
        <br/>
        <p style="color:#333;font-weight: bold;">作者: {{article.author.loginname}}</p>
        <br/>        
        <div v-html="article.content" style="text-align: left;text-indent: 2em;line-height: 2em;padding: 0 1em;" class="content"></div>
    </div>    
</template>

<script>
    import axios from 'axios';
    export default {
        name: 'Article',
        data() {
            return {
                article: {
                    author: {},
                },
            }
        },
        computed: {
            pathUrl : function(){
                return this.$route.query.id;
            }
        },
        mounted(){
            axios({
                method: 'get',
                url: 'https://www.vue-js.com/api/v1/topic/'+this.pathUrl,
            }).then((res)=>{
                this.article = res.data.data;
            });
        }
    }
</script>

<style scoped lang="scss">
    @import '@/assets/scss/base.scss';

    .container{
        width: 100%;
        max-width: $maxWidth;
        background-color: #fff;
        overflow: hidden;
        h1{
            color: red;
            text-align: center;
        }
        img {
            max-width: $maxWidth;
        }
    }
</style>



