<template>
    <div class="container">
        <div class="header">
            <div class="logo">
                <img src="../../static/images/vue.png" alt="">
                <span>Vue.js</span>
                <div class="search fa fa-search" v-bind:class="{changeBg:isFocus}"><input type="text" @blur="isFocus=false;searchInfo=''" @focus="isFocus=true" blur v-model.trim="searchInfo"></div>
            </div>
        </div>
        <div class="play">
            <!-- Swiper -->
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <router-link to="/">全部</router-link>
                    </div>
                    <div class="swiper-slide">
                        <router-link to="/index/good">精华</router-link>
                    </div>
                    <div class="swiper-slide">
                        <router-link to="/index/weex">weex</router-link>
                    </div>
                    <div class="swiper-slide">
                        <router-link to="/index/share">分享</router-link>
                    </div>
                    <div class="swiper-slide">
                        <router-link to="/index/ask">问答</router-link>
                    </div>
                    <div class="swiper-slide">
                        <router-link to="/index/job">招聘</router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="marginT"></div>
        <router-view v-if="!isFocus"></router-view>
        <search :searchInfo="searchInfo" @hideMe="isShow = false" @showMe="isShow = true" v-show="isFocus && isShow"></search>
    </div>
</template>

<script>
    import Search from '@/components/Search';
    export default {
        data(){
            return {
                isFocus: false,
                isShow: true,
                searchInfo: '',
            }
        },
        components: {
            Search,
        },
        mounted() {
            var swiper = new Swiper('.swiper-container', {
                slidesPerView: 5,
            });
            var as = document.querySelectorAll('.swiper-slide');
            for (let i = 0; i < as.length; i++) {
                as[i].onclick = function () {
                    for (var j = 0; j < as.length; j++) {
                        as[j].childNodes[0].style.color = '#217d09';
                        as[j].childNodes[0].style.backgroundColor = 'transparent';
                    }
                    this.childNodes[0].style.color = '#fff';
                    this.childNodes[0].style.backgroundColor = '#368f14';
                }
            }
        }
    };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import "../assets/scss/base.scss";
    .container {
        margin: 0 auto;
        width: 100%;
        max-width: $maxWidth;
        .marginT{
            margin-top: rem(190px);
        }
        .header {
            position: fixed;
            top: 0;
            z-index: 99;
            width: 100%;
            max-width: $maxWidth;
            background-color: #19612f;
            .logo {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                padding: rem(15px) rem(40px);
                img {
                    margin-right: rem(28px);
                    width: rem(80px);
                    height: rem(70px);
                    align-self: center;
                }
                span {
                    margin-right: rem(20px);
                    font-weight: 700;
                    font-size: rem(36px);
                    align-self: center;
                    color: #fff;
                    font-style: oblique;
                    font-stretch: ultra-condensed;
                }
                .search {
                    margin: 0 auto;
                    width: rem(428px);
                    height: rem(46px);
                    line-height: rem(46px);
                    background-color: #4d9635;
                    border-radius: rem(23px);
                    text-align: left;
                    text-indent: rem(16px);
                    font-size: rem(30px);
                    color: #326422;
                    align-self: center;
                    transition: background-color .8s;
                    input{
                        background-color: transparent;
                        border: none;
                        outline: none;
                        line-height: rem(26px);
                        font-size: rem(26px);
                        color: #666;
                        text-indent: rem(6px);
                        vertical-align: center;
                    }
                }
                .changeBg{
                    background-color: #fff;
                }
            }
        }
        .play {
            position: fixed;
            top: rem(100px);
            z-index: 99;
            width: 100%;
            max-width: $maxWidth;
            .swiper-container {
                width: 100%;
                height: 100%;
                background-color: #f6f6f6;
            }
            .swiper-slide {
                text-align: center;
                font-size: 18px;
                height: rem(84px);

                /* Center slide text vertically */
                display: -webkit-box;
                display: -ms-flexbox;
                display: -webkit-flex;
                display: flex;
                -webkit-box-pack: center;
                -ms-flex-pack: center;
                -webkit-justify-content: center;
                justify-content: center;
                -webkit-box-align: center;
                -ms-flex-align: center;
                -webkit-align-items: center;
                align-items: center;
                border-color: transparent;
                &:first-child {
                    a {
                        color: #fff;
                        background-color: #368f14;
                    }
                }
                a {
                    display: block;
                    padding: 0 rem(10px);
                    font-size: rem(28px);
                    line-height: rem(50px);
                    color: #217d09;
                    text-decoration: none;
                    border-radius: rem(8px);
                }
            }
        }
    }

</style>



