import axios from 'axios';

function Data() {
    this.getData = function(param){
        var newParam = Object.assign({
            limit: 20,
        },param);
        return axios({
            method: 'get',
            url: 'https://www.vue-js.com/api/v1/topics',
            params: newParam,
        })
    }
}
var ArticleData = new Data();
export {ArticleData};


