<template>
    <div id="hello">
        <loading v-show="!allArticles"></loading>
        <ul>
            <li class="items" v-for="(item,i) in allArticles" :key="i">
                <img class="avatar" v-lazy="item.author.avatar_url" alt="">
                <div class="tab" :class="{tabbg : item.top||item.good}" v-show="item.top||item.good">
                    <span v-if="item.top">置顶</span>
                    <span v-else-if="item.good">精华</span>
                    <span v-else>{{tabs[item.tab]}}</span>
                </div>
                <div class="title">
                    <router-link :to="{path:'/detail',query:{id:item.id}}" tag="span">{{item.title}}</router-link>
                    <p>{{item.reply_count}}
                        <span> / </span>{{item.visit_count}}</p>
                </div>
                <div class="date">{{item.create_at | moment}}</div>
            </li>
        </ul>
    </div>
</template>

<script>
    import moment from 'moment';
    import Loading from '../loading';
    export default {
        name: 'Two',
        props: ['allArticles'],
        components: {
            Loading
        },
        data() {
            return {
                tabs: {
                    good: '精华',
                    weex: 'weex',
                    share: '分享',
                    ask: '问答',
                    job: '招聘',
                },
            }
        },
        filters: {
            moment: (val) => {
                return moment(val).format('YYYY MMM Do');
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import "../../../assets/scss/base.scss";
    #hello {
        width: 100%;
        background-color: #fff;
        ul {
            li {
                position: relative;
                display: flex;
                margin-bottom: rem(18px);
                padding: rem(18px) rem(18px) 0;
                img {
                    margin-right: rem(28px);
                    width: rem(62px);
                    height: rem(62px);
                    align-self: center;
                }
                .tab {
                    margin-right: rem(28px);
                    padding: rem(8px) rem(12px);
                    font-size: rem(28px);
                    align-self: center;
                    white-space: nowrap;
                    background-color: #e4e4e4;
                    border-radius: rem(8px);
                }
                .tabbg {
                    background-color: #309312;
                    color: #fff;
                }
                .title {
                    width: rem(440px);
                    font-size: rem(28px);
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    align-self: flex-end;
                    text-align: left;
                    p {
                        margin-top: rem(4px);
                        font-size: rem(22px);
                        color: #999;
                        letter-spacing: rem(1px);
                        span {
                            color: #333;
                        }
                    }
                }
                .date {
                    position: absolute;
                    right: rem(18px);
                    bottom: 0;
                    white-space: nowrap;
                    align-self: flex-end;
                    color: #666;
                }
            }
        }
        .no-more{
            font-size: rem(30px);
            color: red;
        }
    }
</style>



